@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;display:flex;flex-direction:column;align-items:center;border-radius:10px;overflow:hidden}.App-logo{height:40vmin;pointer-events:none}img{display:flex;width:100%;height:100%;align-items:center}.test{background-color:#61dafb}.time-is-over{color:red}#footer{position:fixed;bottom:0;width:100%;box-shadow:0 -1px 5px 0px #000;background:#fff;z-index:1;padding:5px 0}@media(prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
#header{display:flex;justify-content:space-around;align-items:center;width:100%;background-color:#ca748b;box-shadow:0 0px 15px 1px #000;padding:5px 0;height:40px;position:fixed;z-index:100}.head-button{width:100%;height:100%;background-color:#000;border-radius:5px;border:none;color:#f37329;font-family:"Otomanopee One",sans-serif;cursor:pointer}a{font-size:18px;display:flex;align-items:center;height:100%;text-decoration:none;justify-content:center;color:#94c571}.log-out{font-size:12px}
.cutlery{display:flex;align-items:center;justify-content:flex-start;width:50%}.choise-check{width:60%;height:40px;text-align:center;border-radius:10px;border:none;font-family:"Otomanopee One",sans-serif;cursor:pointer}.choise-checked{width:65%;height:40px;text-align:center;border-radius:10px;border:none;background-color:#3f52b5;color:#fff;cursor:pointer}
.bread{display:flex;align-items:center;justify-content:flex-end;width:50%}.choise-check{width:65%;height:40px;text-align:center;border-radius:10px;border:none;background-color:#3f52b5;color:#fff;cursor:pointer}
